.midFinal-container {
  display: flex;
  flex-direction: row;
  min-height: 80vh;
  background: #f9f9f9;
}

.sidebar-container-wrapper {
  width: 28%; 
  background-color: rgb(241, 241, 241);
  display: flex;
  justify-content: center;
  position: relative;
}

.close-btn {
  display: none;
}


.burger-btn, .logo2 {
  display: none;
}


.side-midFinal {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.page-title {
  text-align: center;
  font-size: 26px;
  margin-top: 30px;
  color: #333;
}

.subtitle {
  text-align: center;
  font-size: 16px;
  color: #777;
  margin-top: 8px;
}


.main-content.with-shadow {
  border-radius: 16px;
  padding: 30px 40px;
  max-width: 900px;
  margin: 20px auto 50px auto;
  height: 85vh;
}

.emoji-container {
  font-size: 50px;
  text-align: center;
  user-select: none;
  background-color: white;
  padding: 3%;
}


.term-vertical {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}



.term-btn {
  width: 100%;
  height: 80px;
  background-color: #44E5B2;
  border: none;
  border-radius: 16px;
  font-size: 18px;
  font-weight: 500;
  color: white;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(0,0,0,0.15);
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.term-btn:hover {
  background-color: #2dc6a1;
  transform: translateY(-3px);
}

.back-btn {
  position: sticky;
  bottom: 0px;
  align-self: flex-start;
  margin-left: 20px;
  background: none;
  border: none;
  font-size: 20px;
  font-weight: 600;
  color: #44E5B2;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s ease;
  width: 100%;
  text-align: start;
  height: 10vh;
  background-color: white;
  padding-left: 10%;
}

.back-btn:hover {
  color: #2dc6a1;
}


@media (max-width: 767px) {

  .sidebar-container-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 70%;
    max-width: 300px;
    background: rgb(241, 241, 241);
    box-shadow: 2px 0 5px rgba(0,0,0,0.3);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 999;
  }
  .sidebar-container-wrapper.open {
    transform: translateX(0);
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .close-btn {
  display: flex;
  position: absolute;
  justify-content: end;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 24px;
  cursor: pointer;
  padding: 5px;
  color: #333;
  z-index: 1000;
  }
  .header-mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #44E5B2;
    padding: 10px;
  }
  .logo2 {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    display: flex;
    margin-right: 3%;
  }
  .burger-btn {
    display: flex;
    background: transparent;
    border: none;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    cursor: pointer;
  }
  .burger-line {
    width: 100%;
    height: 3px;
    background: white;
    border-radius: 2px;
  }

  .side-midFinal > .header-container {
    display: none;
  }
  .main-content.with-shadow {
    margin: 20px 15px 50px 15px;
    max-width: 100%;
    padding: 20px;
  }
  .term-vertical {
    width: 100%;
    max-width: none;
    margin: 30px 0;
  }
  .term-btn {
    height: 60px;
    font-size: 16px;
  }
  .back-btn {
    margin-left: 15px;
    font-size: 14px;
  }
}
